<template>
    <div class="document">
        <my-layout>
            <!-- 插入左侧导航栏的内容 -->
            <template v-slot:aside>
                <section>
                    <div class="left-menu-search">
                        <p class="version">
                            latest
                        </p>
                        <el-input
                                placeholder="Search Docs"
                                v-model="text">
                            <i slot="prefix" class="el-input__icon el-icon-search"></i>
                        </el-input>
                    </div>
                    <el-menu :class="db.className"  :default-openeds = "db.openeds"  @open="handleOpen" @select="dealSelect" @close="handleClose">
                        <my-item v-for="item in db.data" :key="item.id" :item="item"></my-item>
                    </el-menu>
                </section>
            </template>

            <!-- 插入主内容区的内容 -->
            <template v-slot:main>
                <div>
                    <p class="txt">{{$t('common.total')}}</p>
                    <component v-if="needData.dialogShow" :is="needData.loadComponent"  :passData = "needData"></component>
                </div>
            </template>
        </my-layout>
    </div>
</template>

<script>
  import MyLayout from '@/views/MyLayout.vue'
  import staticDb from '../db/staticDb.js'
  import MyItem from '@/components/MyItem.vue';
  const mustArgs = {dialogShow:false,loadComponent:''};
  export default {
    name: 'DocumentView',
    components: {
      MyLayout,MyItem,
      Parts101: () => import('@/components/doc/Parts101.vue'),
      Parts102: () => import('@/components/doc/Parts102.vue'),
      Parts103: () => import('@/components/doc/Parts103.vue'),
      Parts104: () => import('@/components/doc/Parts104.vue'),
      Parts201: () => import('@/components/doc/Parts201.vue'),
      Parts202: () => import('@/components/doc/Parts202.vue'),
      Parts203: () => import('@/components/doc/Parts203.vue'),
      Parts204: () => import('@/components/doc/Parts204.vue'),
      Parts205: () => import('@/components/doc/Parts205.vue'),
      Parts206: () => import('@/components/doc/Parts206.vue'),
      Parts207: () => import('@/components/doc/Parts207.vue'),
      Parts208: () => import('@/components/doc/Parts208.vue'),
      Parts301: () => import('@/components/doc/Parts301.vue'),
      Parts302: () => import('@/components/doc/Parts302.vue'),
      Parts303: () => import('@/components/doc/Parts303.vue'),
      Parts304: () => import('@/components/doc/Parts304.vue'),
      Parts401: () => import('@/components/doc/Parts401.vue'),
      Parts402: () => import('@/components/doc/Parts402.vue'),
      Parts403: () => import('@/components/doc/Parts403.vue'),
      Parts404: () => import('@/components/doc/Parts404.vue'),
      Parts405: () => import('@/components/doc/Parts405.vue'),
      Parts406: () => import('@/components/doc/Parts406.vue'),
      Parts407: () => import('@/components/doc/Parts407.vue'),
      Parts408: () => import('@/components/doc/Parts408.vue'),
      Parts409: () => import('@/components/doc/Parts409.vue'),
      Parts410: () => import('@/components/doc/Parts410.vue'),
      Parts411: () => import('@/components/doc/Parts411.vue'),
      Parts412: () => import('@/components/doc/Parts412.vue'),
      Parts413: () => import('@/components/doc/Parts413.vue'),
      Parts414: () => import('@/components/doc/Parts414.vue'),
      Parts501: () => import('@/components/doc/Parts501.vue'),
      Parts601: () => import('@/components/doc/Parts601.vue'),
    },
    data() {
      return {
        needData:{...mustArgs},
        text:'',
        db: {
          className: 'aside-nav',
          openeds : ['s100','s300'],
          data: []
        }
      };
    },
    methods:{
      handleOpen(key, keyPath) {
        console.log('开', keyPath);
      },
      dealSelect(key, keyPath) {
        console.log('选择', keyPath);
        try{
          const componentFlag = keyPath[keyPath.length - 1];
          const data = {dialogShow:true,loadComponent:`Part${componentFlag}`};
          this.needData = {...mustArgs,...data}
        }
        catch (e){
          console.log(e)
        }
      },
      handleClose(key, keyPath) {
        console.log('关', keyPath);
      }
    },
    created(){
       this.db.data = staticDb.asideMenu;
    }
  };
</script>

<style lang="scss">
    .aside_nav{
        &>section{width: 350px;}
        .left-menu-search{padding: 0 30px 24px;}
        .el-submenu__title{padding-left:30px !important; font-weight: bold;}
        .el-menu-item{white-space: nowrap; overflow: hidden;text-overflow: ellipsis;}
        .el-menu{border-right: none;}
    };
</style>
